<template>
	<section id="signin" class=" d-flex align-items-center">

    <div class="container">
      <div class="row">
        <div class="col-lg-7 d-flex flex-column justify-content-center">
          <div class="signin-lgo">
         <img src="../assets/images/workwire.png">
       </div>


        </div>

           <div class="col-lg-5 d-flex  justify-content-center align-items-center pt-4 ">

           <div class="signin-box">
            <div class="sub-text">
            <h4> Join Workshires</h4>
            <h5> Choose a display name</h5>
            <div class="join-workshire">
              <span>{{ SuccessMsg }}</span>
              <form v-on:submit.prevent="onSubmit" method="post" class="body" name="myForm">
                 <div class="form-group">
              
            <input type="text" name="userName" class="form-control" placeholder="Username" v-model="posts.userName" required>
             </div>
            

            <div class="box-join">
            <button type="submit" class="join-signin"> Join </button>
         </div>
         </form>
         <div class="botom-trms">
            <p> By joining, you agree to Workshires’s <a href="#"> Terms 
of Service, </a>
as well as to receive occasional 
emails from us.</p>

</div>
</div>
<router-link class="alradt-acount" to="/signin-form"> <label>Already have an account? </label> <span>Sign in.</span> <img src="../assets/images/back-arow.png"></router-link>




           </div>

     </div>
           </div>


        <!-- <div class="col-lg-6 order-1 order-lg-2 hero-img" >
          <img src="../assets/images/sliderbg.png" class="img-fluid " alt="">
        </div> -->
      </div>
    </div>

  </section>

</template>
<script>
    import { putRequest } from './ApiFunctions/index';

	export default {
  		name: 'Signupformstep3',
  		data() {
        return {
            posts: {
                userName:"",
                
            },
            errorMsg:"",
            emailRequired:"",
            passwordRequired:"",
            SuccessMsg:"",
            user_id:"",
            isLoggedIn:true,

        }
        
    },
    
    mounted(){
        console.log("$hostname",this.$hostname);
        if(this.$hostname){
            this.$router.push('/');
        }
    }, 
  		methods: {
        async onSubmit() {
           //alert("hello");
            console.log("posts",this.$route.params.id);
            
            var register = await putRequest('/update-user/'+this.$route.params.id, this.posts);
            const myJSON = JSON.stringify(register);
            
            
             console.log("posts",register);
             this.user_id = true;
             this.$swal('User Registered Successfully');
             
             
  
        },

    }
  	}	

</script>


